.container {
  width: 400px;
  text-align: center;
  margin: 0 auto;
}

img {
  max-width: 100%;
  margin: 0 auto;
}

.timer {
  background-color: #dcf6f6;
  justify-content: center;
  gap: 10px;
  margin-top: 50px;
  display: flex;
}

.field {
  text-align: center;
  width: 80px;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  padding: 5px;
  display: flex;
}

.value {
  background-color: azure;
  border-radius: 10px;
  font-size: 25px;
}

.label {
  color: #1cc31c;
}

.btn-field {
  justify-content: center;
  gap: 30px;
  margin-top: 10px;
  display: flex;
}

